.ot-loader {
    display: inline-block;
    position: relative;
    margin: 1.5em 0;
    width: 64px;
    height: 64px;
}
.ot-loader div {
    box-sizing: border-box;
    display: block;
    position: absolute;
    width: 51px;
    height: 51px;
    margin: 6px;
    border: 6px solid teal;
    border: 6px solid var(--primary-color,teal);
    border-radius: 50%;
    -webkit-animation: ot-loader 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;
            animation: ot-loader 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;
    border-color: teal transparent transparent transparent;
    border-color: var(--primary-color,teal) transparent transparent transparent;
}

.ot-loader.thin div{
    border-width: 3px;
    width: 42.5px;
    height: 42.5px;
}

.ot-loader div:nth-child(1) {
    -webkit-animation-delay: -0.45s;
            animation-delay: -0.45s;
}
.ot-loader div:nth-child(2) {
    -webkit-animation-delay: -0.3s;
            animation-delay: -0.3s;
}
.ot-loader div:nth-child(3) {
    -webkit-animation-delay: -0.15s;
            animation-delay: -0.15s;
}
@-webkit-keyframes ot-loader {
    0% {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    100% {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}
@keyframes ot-loader {
    0% {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    100% {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}
