.ot-woza-item{
    background: #fff;
    min-height: 80px;
    overflow: hidden;
    border-radius: 10px;
    position: relative;
    overflow: hidden;
    border: 1px solid #ddd;
}

.ot-woza-item-title{
    position: absolute;
    top: 0.5em;
    left: 0.5em;
    z-index: 1;
}

.ot-woza-item-title .ot-dp{
    margin-right: 0.8em;
    border: 2px solid #fff;
}

.ot-woza-item-title .ot-woza-item-owner{
    font-weight: bold;
    display: block;
    margin-bottom: 0.06em;
    text-transform: capitalize;
}

.ot-woza-item-date{
    font-size: 0.85em;
    color: #555;
}

.ot-woza-item-progress-items{
    display: flex;
    position: absolute;
    top: 1em;
    left: 0.5em;
    right: 0.5em;
    z-index: 10;
}

.ot-woza-item-progress-items .ot-woza-item-progress{
    flex: 1;
}

.ot-woza-item-progress-items .ot-woza-item-progress + .ot-woza-item-progress{
    margin-left: 0.7em;
}

.ot-woza-item-progress{
    width: 100%;
    position: relative;
    height: 5px;
    border-radius: 2px;
    background: #ccc;
}

.ot-woza-item-progress-bar{
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    transform: scaleX(0);
}

@media screen and (max-width: 540px){
    .ot-woza-item-title {
        
    }
}

@media screen and (max-width: 350px){
    .ot-woza-list{
        padding: 0.25em 0;
    }
    
    .ot-woza-list .ot-woza-item-wrapper{
        margin-bottom: 0;
    }
    
    .ot-woza-item-wrapper:nth-child(even){
        padding-left: 0.25em;
    }
    
    .ot-woza-item-wrapper:nth-child(odd){
        padding-right: 0.25em;
    }
}